.popular {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 10px;
  margin: 20px;
  /* height: 90vh; */
}
.popular h2 {
  color: #171717;
  font-size: 50px;
  font-weight: 600;
}

.popular hr {
  width: 200px;
  height: 6px;
  border-radius: 10px;
  background-color: #252525;
}
.popular-item {
  margin-top: 50px;
  display: flex;
  gap: 30px;
}
@media (max-width: 768px) {
  .popular h2 {
    font-size: 30px;
    font-weight: 400;
  }
  .popular-item{
    flex-direction: column;
  }
}
